import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import KSVNavBar from './components/navbar/KSVNavBar.component';
import Home from './components/home/Home.component';
import { Route, BrowserRouter as Router } from 'react-router-dom';
import Switch from 'react-bootstrap/esm/Switch';
import Standings from './components/standings/Standings.component';
import Contact from './components/contact/Contact.component';
import KSVFooter from './components/footer/KSVFooter.component';

const App = () => {
  return (
    <Router>
      <KSVNavBar />

      <div className="container mt-10vh">
        <Switch>
          <Route path="/about">
            <h2>About</h2>
          </Route>
          <Route path="/schedule">
            <h2>Schedule</h2>
          </Route>
          <Route path="/standings">
            <Standings />
          </Route>
          <Route path="/contact">
            <Contact />
          </Route>
          <Route exact path="/">
            <Home />
          </Route>
        </Switch>
      </div>
      <KSVFooter />
    </Router>
  );
};

export default App;
